<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb">
      <Back></Back>
      <Breadcrumb :list="routes" style="flex-grow: 1;margin-left: 1rem;"></Breadcrumb>
      <TspButton type="cancel" name="取消" margin="true" @onClick="onCancel"></TspButton>
      <TspButton type="confirm" name="确定" :margin="false" @onClick="onOk"></TspButton>
    </div>
    <div class="tsp-content ">
      <div class="tsp-content-main">
        <div class="tsp-flex-start">
          <div class="tsp-pipe"></div>
          <div style="margin-left: 1rem"><b>编辑详情</b></div>
        </div>
        <div class="tsp-layout-flex" style="margin-top: 1rem">
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">车厂名称</div>
            <el-select class="tsp-ae-input" v-model="po.factoryName"></el-select>
          </div>
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">{{activeIndex=='0' ? '接入平台' : '检测平台'}}</div>
            <el-select class="tsp-ae-input" v-model="po.platform"></el-select>
          </div>
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">账号</div>
            <el-input class="tsp-ae-input" clearable v-model="po.account"></el-input>
          </div>
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">密码</div>
            <el-input class="tsp-ae-input" clearable v-model="po.pwd"></el-input>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb"
  import TspDialog from "@/components/custom/ConfirmDialog"
  import Back from "@/components/custom/Back"

  export default {
    name: 'AccountEdit',
    components: {
      TspButton,
      Breadcrumb,
      TspDialog,
      Back
    },
    data() {
      return {
        po: {},
        routes: [],
        activeIndex: 0
      }
    },
    methods: {
      onOk() {
        this.post(this.urlEditAccount, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.$router.push({name: 'AccountManagement', params: {storeParam: {platform: this.activeIndex}}});
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onCancel() {
        this.$router.push({name: 'AccountManagement', params: {storeParam: {platform: this.activeIndex}}});
      },
      getDataList() {
        this.post(this.urlListAccounts, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
    },
    mounted() {
      this.activeIndex = this.routeParams().platform;
      this.routes = [
        {
          name: '账号管理',
          path: 'AccountManagement',
        },
        {
          name: this.activeIndex == '0' ? '接入平台' : '检测平台',
          path: 'AccountManagement',
        },
        {
          name: '添加',
          path: 'AccountManagement',
        },
      ];
    }
  }
</script>

<style scoped>
  .tsp-ae-input {
    width: 16.875rem;
  }

  .tsp-ae-item {
    min-width: 48.1875rem;
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
  }

  .tsp-ae-subtitle {
    width: 5rem;
    margin-right: 2.25rem;
  }

  .tsp-content-main {
    background: white;
    border-radius: 0.5rem;
    padding: 1.25rem;
    padding-bottom: 6.25rem;
  }

  .tsp-content {
    width: 100%;
    padding: 1rem;
  }
</style>



