<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb" style="background: transparent;">
      <b style="flex-grow: 1;">接入配置</b>
      <SearchBox placeholder="" v-on:onSearch="onSearch" :maxlength="10"></SearchBox>
    </div>
    <div class="tsp-content">
      <el-table :header-row-style="{background:'#efefef'}" :header-cell-style="{background:'#efefef'}"
                :data="dataList.resultList" stripe>
        <el-table-column type="selection" header-align="center" align="center" width="80">
        </el-table-column>
        <el-table-column label="序号" type="index" width="100">
        </el-table-column>
        <el-table-column label="VIN码" min-width="150">
          <template slot-scope="scope">
            <div>
              {{scope.row.name }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="ICCID号" min-width="180">
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createUser }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车型" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车款" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车辆状态" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车厂" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="地方平台" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="国家平台" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="基础操作" min-width="150" align="center">
          <template slot-scope="scope">
            <div>
              <span class="tsp-bt tsp-bt-manage">配置平台</span>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div class="tsp-flex-end tsp-pagination">
        <el-pagination
          background
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          :total="dataList.results">
        </el-pagination>
      </div>
    </div>

    <TspDialog :isShow="dialog.isShow">
      <div class="mp-ac-dialog-config">
        <div class="tsp-flex-end tsp-click" @click="dialog.isShow=false">x</div>
        <div class="tsp-flex mp-ac-dialog-config-title">配置平台</div>
        <div class="tsp-flex mp-ac-dialog-config-content">
          <el-select placeholder="请选择">
            <el-option
              v-for="item in platforms"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="tsp-flex" style="margin-bottom: 1.875rem">
          <el-button type="primary" style="margin-right: 1rem;font-size: 1.1rem;">
            &nbsp;&nbsp;确&nbsp;&nbsp;定&nbsp;&nbsp;
          </el-button>
          <el-button type="info" style="margin-left: 1rem;font-size: 1.1rem;">&nbsp;&nbsp;取&nbsp;&nbsp;消&nbsp;&nbsp;
          </el-button>
        </div>
      </div>
    </TspDialog>
  </div>
</template>

<script>
  import SearchBox from "@/components/custom/SearchBox"
  import TspButton from "@/components/custom/Button"
  import TspDialog from "@/components/custom/Dialog"

  export default {
    name: 'AccessConfig',
    components: {
      SearchBox,
      TspButton,
      TspDialog
    },
    data() {
      return {
        keyWord: '',
        index: 0,
        pageSize: 10,
        ascending: null,
        dataList: {
          results: 0,
          pageCount: 0,
          pageSize: 10,
          currentPage: 1,
          resultList: [{}]
        },
        selects: [],
        dialog: {
          isShow: true,
        },
      }
    },
    methods: {
      showDialog() {
        this.deleteObj.isShow = true;
        if (type == 'confirm') {
          this.deleteObj.type = type;
        } else {
          this.deleteObj.type = type;
        }
      },
      onSearch(words) {
        this.keyWord = words;
        this.index = 0;
        this.getDataList();
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getDataList();
      },
      handleCurrentChange(val) {
        this.index = val - 1;
        this.getDataList()
      },
      getDataList() {
        this.post(this.urlListAccounts, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      handleSelectionChange(val) {
        this.selects = val;
      },
    },
    mounted() {
      this.getDataList();
    }
  }
</script>

<style scoped>
  .mp-ac-dialog-config-title {
    font-size: 2rem;
    font-weight: bold;
  }

  .mp-ac-dialog-config-content {
    margin-top: 5.625rem;
    margin-bottom: 5.625rem;
  }

  .mp-ac-dialog-config {
    background: white;
    border-radius: 1.5rem;
    padding: 1rem;
    width: 33.5rem;
  }

  .tsp-content {
    width: 100%;
  }

  .tsp-pagination {
    padding: 1rem;
  }
</style>



