<template>
  <div class="tsp-login-bg">
    <div class="tsp-login-flex tsp-login-input-area">
      <div class="tsp-login-flex tsp-login-input-area-left">
        <div class="tsp-login-input-area-forget-pwd">
          <span @click="isSmsMode=true" v-if="!isSmsMode">您是否忘记密码？</span>
          <span @click="isSmsMode=false" v-else>返回登录</span>
        </div>
      </div>
      <div class="tsp-login-flex tsp-login-input-area-right">
        <div class="tsp-login-input-area-right-title">{{isSmsMode?"忘记密码":"登录"}}</div>
        <div class="tsp-login-input-area-right-subtitle" style="margin-bottom: 0.5rem">{{isSmsMode?"手机号":"用户名"}}</div>
        <TspInput placeholder="请输入手机号" v-model="po.phone" class="tsp-login-input-area-right-item" :maxlength="11"
                  v-show="isSmsMode"></TspInput>
        <TspInput placeholder="请输入用户名" v-model="po.username" class="tsp-login-input-area-right-item"
                  v-show="!isSmsMode"></TspInput>
        <div class="tsp-login-input-area-right-subtitle" style="margin-bottom: 0.5rem;margin-top: 2rem">
          <span>密码</span>
        </div>
        <TspInput placeholder="请输入密码" v-show="!isSmsMode" class="tsp-login-input-area-right-item" type="password"
                  @onKeyup="onKeyUp"
                  v-model="po.pwd"></TspInput>
        <TspInput placeholder="输入验证码" v-model="po.code" class="tsp-login-input-area-right-item"
                  v-show="isSmsMode">
          <div slot="after" class="tsp-login-input-area-right-send-code" @click="onSendCode"
               :style="{background: isSend?'#9fa4ad':'#3a8ee6'}">{{isSend?countdown:'发送验证码'}}
          </div>
        </TspInput>
        <div class="tsp-login-input-area-right-login" @click="onConfirm">登 录</div>
      </div>
    </div>
  </div>
</template>

<script>
  import TspInput from "@/components/custom/Input.vue"

  export default {
    name: 'Login',
    data() {
      return {
        po: {
          username: this.localStore('username'),
          phone: '',
          pwd: '',
          isRemember: true
        },
        isSmsMode: false,
        isSend: false,
        count: 60
      }
    },
    components: {
      TspInput
    },
    computed: {
      countdown() {
        return this.count
      }
    },
    methods: {
      check() {
        if (!this.po.username) {
          this.$message({showClose: true, message: '请填写用户名！', type: 'warning'});
          return false
        }
        return true
      },
      onSendCode() {
        console.log(this.po);
        if (this.isPhoneNumber(this.po.phone)) {
          if (!this.isSend) {
            this.isSend = true;
            this.count = 60;
            let id = setInterval(() => {
              this.count = this.count - 1;
              if (this.count <= 0) {
                this.isSend = false;
                clearInterval(id);
              }
            }, 1000);
            this.postCode();
          }
        } else {
          this.$message({showClose: true, message: '手机号不正确！', type: 'warning'});
        }
      },
      onKeyUp(event) {
        if (event.keyCode == 13) {
          this.onConfirm();
        }
      },
      postCode() {
        this.$http.post(this.tspSendPassword, {
          phone: this.po.phone,
        }, {
          emulateJSON: true,
        }).then(function (res) {
          if (res.data.success) {
            if (!res.data.success) {
              this.$message({
                showClose: true,
                message: "<b style=\"color: black\">错误 " + res.data.code + "：</b>" + "" + res.data.message,
                duration: 60000,
                type: 'error'
              });
            }
          } else {
            if (!res.data.success) {
              this.$message({
                showClose: true,
                message: "<b style=\"color: black\">错误 " + res.data.code + "：</b>" + "" + res.data.message,
                duration: 60000,
                type: 'error'
              });
            }
          }
        }, function (res) {
          this.$message({showClose: true, message: '服务器异常，请检查网络！', type: 'error'});
        });
      },
      onConfirm() {
        if (this.isSmsMode) {
          this.$http.post(this.tspCodeLogin, {
            phone: this.po.phone,
            password: this.po.code,
          }, {
            emulateJSON: true,
          }).then(function (res) {
            if (res.data.success) {
              this.localStore(this.keyToken, JSON.stringify(res.data.data));
              this.$router.push("/");
            } else {
              if (!res.data.success) {
                this.$message({
                  showClose: true,
                  message: "<b style=\"color: black\">错误 " + res.data.code + "：</b>" + "" + res.data.message,
                  duration: 60000,
                  type: 'error'
                });
              }
            }
          }, function (res) {
            this.$message({showClose: true, message: '服务器异常，请检查网络！', type: 'error'});
          });
        } else {
          if (this.check()) {
            this.$http.post(this.urlLogin,
              {
                username: this.po.username,
                password: this.po.pwd,
              },
              {
                emulateJSON: true,
              }).then(function (res) {
              this.log(res);
              if (res.data.success) {
                this.localStore('username', this.po.username);
                if (this.po.isRemember) {
                  this.localStore('password', this.po.pwd);
                }
                this.localStore(this.keyToken, JSON.stringify(res.data.data));
                this.$router.push("/");
              } else {
                if (!res.data.success) {
                  this.$message({
                    showClose: true,
                    message: "<b style=\"color: black\">错误 " + res.data.code + "：</b>" + "" + res.data.message,
                    duration: 60000,
                    type: 'error'
                  });
                }
              }
            }, function (res) {
              this.$message({showClose: true, message: '服务器异常，请检查网络！', type: 'error'});
            });
          }
        }
      },
    },
    mounted() {
    }
  }
</script>

<style scoped>
  .tsp-login-input-area-right-subtitle {
    width: 18.25rem;
  }

  .tsp-login-input-area-right-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 3rem;
  }

  .tsp-login-flex {
    display: flex;
    display: -webkit-flex;
  }

  .tsp-login-input-area-right-send-code {
    color: white;
    cursor: pointer;
    user-select: none;
    white-space: nowrap;
    border-bottom-right-radius: 0.3rem;
    border-top-right-radius: 0.3rem;
    text-align: center;
    font-size: 0.8rem;
    line-height: 2.4375rem;
    margin-left: -1rem;
    z-index: 2;
    width: 7rem;
  }

  .tsp-login-input-area-right-login {
    background: #3a8ee6;
    color: white;
    font-weight: 500;
    text-align: center;
    padding: 0.6rem 0;
    border-radius: 5rem;
    font-size: 1.3rem;
    cursor: pointer;
    user-select: none;
    margin-top: 3.5rem;
    width: 18.25rem;
  }

  .tsp-login-input-area-forget-pwd {
    color: white;
    margin-bottom: 2.625rem;
    border-bottom: 0.1rem solid white;
    padding-bottom: 0.1rem;
    cursor: pointer;
    user-select: none;
  }

  .tsp-login-input-area-right-item {
    width: 18.25rem;
    height: 2.4375rem;
  }

  .tsp-login-input-area-right {
    background: white;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .tsp-login-input-area-left {
    width: 15.3125rem;
    flex-direction: column-reverse;
    align-items: center;
    background: url("../../../assets/logn_area_left.png") no-repeat;
    background-size: auto;
  }

  .tsp-login-input-area {
    width: 48.625rem;
    height: 32.4375rem;
    background: white;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.3);
  }

  .tsp-login-bg {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    background: url("../../../assets/bg_login.png") no-repeat;
    background-size: cover;
  }
</style>



