<template>
  <div class="tsp-flex-vertical">
    <div class="tsp-flex-start tsp-breadcrumb">
      <!--<Breadcrumb :list="routes" style="flex-grow: 1"></Breadcrumb>-->
      <b style="flex-grow: 1;">首页</b>
      <div class="bt bt-parse" style="margin-right: 1rem"> 统计分析</div>
      <div class="bt bt-more">监控更多</div>
    </div>
    <div class="menu tsp-flex-start">
      <div class="menu-item" style="min-width: 23.4375rem">
        <div class="menu-item-card tsp-flex-start">
          <img src="../../../assets/mileage.png" alt="">
          <div style="margin-left:1.25rem;">
            <div class="menu-item-subtitle">总公里数：</div>
            <b class="menu-item-text">1231123</b><span>KM</span>
          </div>
        </div>
      </div>


    </div>
    <div class="map-field" id="map-field">
      <baidu-map id="map" class="baidu-map" :center="map.center" :zoom="map.zoom" :map-click="true"
                 :scroll-wheel-zoom="true"
                 @click="onMapClick">
        <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
        <bm-marker :position="markerPoint" v-if="markerPoint">
        </bm-marker>
      </baidu-map>
    </div>
  </div>
</template>

<script>
  import Tabs from "@/components/custom/Tabs.vue"
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb.vue"

  export default {
    name: 'AccountManagement',
    components: {
      TspButton,
      Breadcrumb,
      Tabs
    },
    data() {
      return {
        dataList: {
          results: 0,
          pageCount: 0,
          pageSize: 10,
          currentPage: 1,
          resultList: []
        },
        map: {
          center: {lat: 30, lng: 118},
          zoom: 13
        },
        markerPoint: {},
      }
    },
    methods: {
      onMapClick(event) {
        this.markerPoint = event.point;
      },
      getDataList() {
        this.post(this.urlAccountList, {}, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
    },
    mounted() {
      this.getDataList();
      document.getElementById("map").style.height = window.getComputedStyle(document.getElementById("map-field")).height;
    },
  }
</script>

<style scoped>
  .baidu-map {
    height: 100%;
  }

  .map-field {
    flex-grow: 1;
  }

  .menu-item-text {
    font-size: 1.6rem;
    margin-right: 0.625rem;
    margin-top: 1rem;
  }

  .menu-item-subtitle {
    color: #a9a9a9;
  }

  .menu-item-card {
    height: 100%;
    align-items: center;
    background: white;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
    border-radius: 0.3rem;
    padding-right: 2.25rem;
    padding-left: 2.25rem;
  }

  .menu-item {
    padding: 0.625rem;
    flex-grow: 1;
    height: 100%;
    min-width: 14.5rem;
  }

  .menu {
    height: 8.9375rem;
    width: 100%;
    padding: 0.625rem;
  }

  .bt {
    height: 2rem;
    padding: 0 1rem;
    font-size: 1rem;
    color: white;
    border-radius: 3rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    white-space: nowrap;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: transparent;
  }

  .bt-parse {
    background: #00b7ee;
  }

  .bt-more {
    background: #46c89f;
  }
</style>



