<template>
  <div class="tsp-tabs-mark">
    <div class="tsp-tabs-pre" @click="onPre()" v-if="width<(list.length*itemWidth)"><</div>
    <div id="tabs-items" class="tsp-tabs-content">
      <div v-for="(item,index) in list" :class="{'tsp-tabs-isActive-color':index==selectIndex}"
           class="tsp-tabs-item">
        <div class="tsp-tabs-name" @click="scrollTo(index)">{{item.name}}</div>
        <div :class="{'tsp-tabs-isActive':index==selectIndex}"></div>
      </div>
    </div>
    <div class="tsp-tabs-next" @click="onNext()" v-if="width<list.length*itemWidth">></div>
  </div>
</template>

<script>
  export default {
    name: 'Tabs',
    data() {
      return {
        selectIndex: 0,
        width: 0,
        itemWidth: 0
      }
    },
    props: {
      list: {
        type: Array,
        default: function () {
          return []
        }
      },
      defaultValue: {
        type: [Object, Number],
        default: function () {
          return 0
        }
      },
    },
    methods: {
      handleWidth() {
        try {
          if (document.getElementById('tabs-items')) {
            this.width = document.getElementById('tabs-items').offsetWidth;
            this.itemWidth = document.getElementsByClassName('tsp-tabs-item')[0].offsetWidth;
            this.width = this.width ? this.width : 0;
            this.itemWidth = this.itemWidth ? this.itemWidth : 0;
          }
        } catch (e) {
          console.log(e)
        }
      },
      scrollTo(index = 0) {
        try {
          this.handleWidth();
          this.selectIndex = index;
          let aheadWidth = parseInt(this.itemWidth) * (parseInt(index) + 1);
          let marginL = 0;
          if (aheadWidth < this.width) {
            marginL = 0;
          } else {
            marginL = this.width - aheadWidth;
          }
          this.onSelect(index);
        } catch (e) {
          console.log(e)
        }
      },
      onPre() {
        if (this.selectIndex > 0) {
          this.scrollTo(this.selectIndex - 1);
        }
      },
      onNext() {
        if (this.selectIndex < this.list.length - 1) {
          this.scrollTo(this.selectIndex + 1);
        }
      },
      onSelect(index) {
        this.$emit('onChange', this.list[index], index);
      },
    },
    watch: {
       defaultValue: {
         handler: function (newVal, oldVal) {
           if (typeof newVal === 'number') {
             this.scrollTo(newVal);
           } else if (typeof newVal === 'object') {
             for (let obj of this.list) {
               if (obj.id == newVal.id) {
                 this.scrollTo(newVal);
                 break;
               }
             }
           }
         },
         immediate: true,
       },
      list: {
        handler: function (newVal, oldVal) {
          if ((this.defaultValue == 'undefined') | (!newVal) | (newVal.length == 0)) {
            return;
          }
          if (typeof this.defaultValue === 'number') {
            this.scrollTo(this.defaultValue);
          } else if (typeof this.defaultValue === 'object') {
            for (let obj in newVal) {
              if (newVal[obj].id == this.defaultValue.id) {
                this.scrollTo(obj);
                return;
              }
            }
            this.scrollTo(0);
          }
        },
        immediate: true,
        deep: true
      }
    },
    updated() {
      this.handleWidth();
    }
  }
</script>
<style scoped>
  * {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }

  .tsp-tabs-mark {
    color: #8195a1;
    position: relative;
    overflow: hidden;
    height: 4.25rem;
    padding: 0 1rem
  }

  .tsp-tabs-content {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100%;
    flex-wrap: nowrap;
  }

  .tsp-tabs-pre {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    background: linear-gradient(left, rgba(0, 0, 0, 0.6), transparent);
    background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0.6)), to(transparent));
    cursor: pointer;
    height: 100%;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    width: 1.5rem;
    color: white;
  }

  .tsp-tabs-next {
    position: absolute;
    cursor: pointer;
    z-index: 10;
    right: 0;
    top: 0;
    height: 100%;
    background: linear-gradient(right, rgba(0, 0, 0, 0.6), transparent);
    background: -webkit-gradient(linear, right top, left top, from(rgba(0, 0, 0, 0.6)), to(transparent));
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    color: white;
    width: 1.5rem;
  }

  .tsp-tabs-isActive-color {
    color: #00b7ee;
  }

  .tsp-tabs-isActive {
    height: 8%;
    background: #00b7ee;
    width: 100%;
  }

  .tsp-tabs-name {
    height: 92%;
    font-size: 1rem;
    cursor: pointer;
    width: 100%;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }

  .tsp-tabs-item {
    height: 100%;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    min-width: 11.25rem;
  }
</style>



