<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb" style="background: transparent;">
      <b style="flex-grow: 1;">平台检测</b>
      <SearchBox placeholder="" v-on:onSearch="onSearch" :maxlength="10"></SearchBox>
      <router-link class="tsp-router-link"
                   :to="{name:'NewPlatformTest'}">
        <TspButton type="add" name="添加" margin="true"></TspButton>
      </router-link>
      <TspButton type="delete" name="删除" :margin="false" @onClick="onDeleteBefore"></TspButton>
    </div>
    <div class="tsp-content">
      <el-table :header-row-style="{background:'#efefef'}" :header-cell-style="{background:'#efefef'}"
                :data="dataList.resultList" stripe
                @selection-change="handleSelectionChange" @filter-change="onFilterChange"
                @sort-change="onSortChange">
        <el-table-column type="selection" header-align="center" align="center" width="80">
        </el-table-column>
        <el-table-column label="序号" type="index" width="100">
        </el-table-column>
        <el-table-column label="检测平台" min-width="150">
          <template slot-scope="scope">
            <div>
              {{scope.row.name }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="车厂名称" min-width="180">
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="VIN码" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createUser }}</span>
          </template>
        </el-table-column>
        <el-table-column label="ICCID号" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="检测状态" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="重新检测" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="导出日志" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="导出检测时间点" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
      </el-table>
      <div class="tsp-flex-end tsp-pagination">
        <el-pagination
          background
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          :total="dataList.results">
        </el-pagination>
      </div>
    </div>

    <DeleteDialog :isShow="deleteObj.isShow" @handleShow="(val)=> this.deleteObj.isShow=val" :param="deleteObj"
                  @handleOk='onOk'
                  @handleCancel='onCancel'></DeleteDialog>
  </div>
</template>

<script>
  import SearchBox from "@/components/custom/SearchBox"
  import TspButton from "@/components/custom/Button"
  import DeleteDialog from "@/components/custom/DeleteDialog.vue"
  import TspDialog from "@/components/custom/ConfirmDialog.vue"

  export default {
    name: 'PlatformTest',
    components: {
      SearchBox,
      TspButton,
      DeleteDialog,
      TspDialog
    },
    data() {
      return {
        keyWord: '',
        index: 0,
        pageSize: 10,
        ascending: null,
        dataList: {
          results: 0,
          pageCount: 0,
          pageSize: 10,
          currentPage: 1,
          resultList: [{}]
        },
        selects: [],
        routes: [],
        deleteObj: {
          isShow: false,
          text: '',
          type:
            'confirm',
        },
      }
    },
    methods: {
      showDialog(type = 'confirm') {
        this.deleteObj.isShow = true;
        if (type == 'confirm') {
          this.deleteObj.type = type;
        } else {
          this.deleteObj.type = type;
        }
      },
      onOk() {
        this.onDelete();
      },
      onCancel() {

      },
      onDeleteBefore() {
        if (this.selects && this.selects.length > 0) {
          this.showDialog();
        } else {
          this.$message({showClose: true, message: '请选择要删除的数据!', type: 'warning'});
        }
      },
      onDelete() {
        let ids = [];
        for (let obj of this.selects) {
          ids.push(obj.id);
        }
        this.post(this.tspQtDQ, {
          ids: ids.toString(),
        }, (res) => {
          if (res.data.success) {
            this.$message({showClose: true, message: '删除成功！', type: 'success'});
            this.getDataList();
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onSearch(words) {
        this.keyWord = words;
        this.index = 0;
        this.getDataList();
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getDataList();
      },
      handleCurrentChange(val) {
        this.index = val - 1;
        this.getDataList()
      },
      getDataList() {
        this.post(this.urlPlatformDetectioList, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      handleSelectionChange(val) {
        this.selects = val;
      },
    },
    mounted() {
      this.getDataList();
    }
  }
</script>

<style scoped>
  .tsp-content {
    width: 100%;
  }

  .tsp-pagination {
    padding: 1rem;
  }
</style>



