<template>
  <div class="tsp-dialog-mask" v-show="isShow" :style="maskStyle">
    <div class="tsp-dialog-bg" :style="dialogStyle">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Dialog',
    data() {
      return {}
    },
    props: {
      isShow: {
        type: Boolean,
        default: function () {
          return false
        }
      },
      maskStyle: {
        type: Object,
        default: function () {
          return {}
        }
      },
      dialogStyle: {
        type: Object,
        default: function () {
          return {
            marginTop: '20%'
          }
        }
      },
    },
  }
</script>
<style scoped>
  .tsp-dialog-bg {
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
    width: fit-content;
    width: -moz-fit-content;
    margin-left: auto;
    margin-right: auto;
    background: white;
    border-radius: 3px;
  }

  .tsp-dialog-mask {
    position: fixed;
    z-index: 2000;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
</style>



