<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb" style="background: #ffffff;">
      <!--<Breadcrumb :list="routes" style="flex-grow: 1"></Breadcrumb>-->
      <b>检测监控</b>
    </div>
    <div class="tsp-flex-start" style="background: white;padding: 0 1rem;width:95%;margin-left: 2.5%;height: 95%;margin-top: 2rem;">
      <div style="flex-grow: 1;">
        <div class="tsp-flex-start tsp-ae-item">
          <div class="tsp-label tsp-ae-subtitle">数据类型</div>
          <el-select class="tsp-ae-input" v-model="po.platform"></el-select>
          <div class="tsp-label tsp-ae-subtitle">报文类型</div>
          <el-select class="tsp-ae-input" v-model="po.platform"></el-select>
          <div class="tsp-label tsp-ae-subtitle">时间</div>
          <el-select class="tsp-ae-input" v-model="po.platform"></el-select>
          <span style="margin-left: 0.5rem;margin-right: 0.5rem">至</span>
          <el-select class="tsp-ae-input" v-model="po.platform"></el-select>
        </div>
        <div class="tsp-flex-start tsp-ae-item">
          <div class="tsp-label tsp-ae-subtitle">VIN码</div>
          <el-input class="tsp-ae-input" v-model="po.platform"></el-input>
          <div class="tsp-label tsp-ae-subtitle">历史数据</div>
          <el-select class="tsp-ae-input" v-model="po.platform"></el-select>
        </div>
      </div>

      <router-link class="tsp-router-link"
                   :to="{name:'PlatformAdd',params: { storeParam: {platform:activeIndex} } }">
        <TspButton type="search" name="查询" margin="true"></TspButton>
      </router-link>
      <TspButton type="export" name="导出" :margin="true" @onClick="onDeleteBefore"></TspButton>
    </div>
    <div class="tsp-content">
      <div style="width: 98%;margin-left:1% ;padding-top: 1rem;">
      <el-table :header-row-style="{background:'#efefef'}" :header-cell-style="{background:'#efefef'}"
                :data="dataList.resultList" stripe border
                @selection-change="handleSelectionChange" @filter-change="onFilterChange"
                @sort-change="onSortChange" >
        <el-table-column type="selection" header-align="center" align="center" width="80">
        </el-table-column>
        <el-table-column label="序号" type="index" width="50">
        </el-table-column>
        <el-table-column label="车牌号" min-width="80" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="VIN码" min-width="120" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="服务器接收时间" min-width="120" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="报文时间" min-width="120" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="报文类型" min-width="100" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="校验" min-width="50" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="报文长度" min-width="100" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="原始报文" min-width="180" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="解析" header-align="center" align='center' width="150">
          <template slot-scope="scope">
            <router-link class="tsp-router-link"
                         :to="{name:'PlatformEdit' ,params: { storeParam: {platform:activeIndex} } }">
              <div class="tsp-bt tsp-bt-edit">解析数据</div>
            </router-link>
          </template>
        </el-table-column>
        <el-table-column label="显示报文" header-align="center" align='center' width="150">
          <template slot-scope="scope">
            <router-link class="tsp-router-link"
                         :to="{name:'PlatformEdit' ,params: { storeParam: {platform:activeIndex} } }">
              <div class="tsp-bt tsp-bt-look">显示全部</div>
            </router-link>
          </template>
        </el-table-column>
      </el-table>
      <div class="tsp-flex-end tsp-pagination">
        <el-pagination
          background
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          :total="dataList.results">
        </el-pagination>
      </div>
    </div>

    <DeleteDialog :isShow="deleteObj.isShow" @handleShow="(val)=> this.deleteObj.isShow=val" :param="deleteObj"
                  @handleOk='onOk'
                  @handleCancel='onCancel'></DeleteDialog>
    </div>
  </div>
</template>

<script>
  import SearchBox from "@/components/custom/SearchBox"
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb.vue"
  import DeleteDialog from "@/components/custom/DeleteDialog.vue"
  import TspDialog from "@/components/custom/ConfirmDialog.vue"

  export default {
    name: 'PlatformManagement',
    components: {
      SearchBox,
      TspButton,
      Breadcrumb,
      DeleteDialog,
      TspDialog
    },
    data() {
      return {
        keyWord: '',
        index: 0,
        pageSize: 10,
        ascending: null,
        activeIndex: '0',
        dataList: {
          results: 0,
          pageCount: 0,
          pageSize: 10,
          currentPage: 1,
          resultList: [{}]
        },
        selects: [],
        routes: [],
        deleteObj: {
          isShow: false,
          text: '',
          type:
            'confirm',
        },
        po: {},
      }
    },
    methods: {
      handleSelect(key, keyPath) {
        this.activeIndex = keyPath[0];
      },
      onFilterChange(filters) {
        console.log(filters);
        if (filters['loginStatus']) {
          this.loginStatus = filters['loginStatus'][0] ? filters['loginStatus'][0] : '';
        }
        this.getDataList()
      },

      onQuery(row) {
        this.post(this.tspRlGAL, {
          id: row.roleId,
        }, (res) => {
          if (res.data.success) {
            this.sharedAuths.dialogVisible = true;
            this.sharedAuths.authList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onSortChange(sort) {
        console.log(sort);
        this.ascending = sort.order;
        this.getDataList()
      },
      showDialog(type = 'confirm') {
        this.deleteObj.isShow = true;
        if (type == 'confirm') {
          this.deleteObj.type = type;
        } else {
          this.deleteObj.type = type;
        }
      },
      onOk() {
        this.onDelete();
      },
      onCancel() {

      },
      onDeleteBefore() {
        if (this.selects && this.selects.length > 0) {
          this.showDialog();
        } else {
          this.$message({showClose: true, message: '请选择要删除的数据!', type: 'warning'});
        }
      },
      onDelete() {
        let ids = [];
        for (let obj of this.selects) {
          ids.push(obj.id);
        }
        this.post(this.tspQtDQ, {
          ids: ids.toString(),
        }, (res) => {
          if (res.data.success) {
            this.$message({showClose: true, message: '删除成功！', type: 'success'});
            this.getDataList();
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },

      onSearch(words) {
        this.keyWord = words;
        this.index = 0;
        this.getDataList();
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getDataList();
      },
      handleCurrentChange(val) {
        this.index = val - 1;
        this.getDataList()
      },
      getDataList() {
        this.post(this.urlListAccounts, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      handleSelectionChange(val) {
        this.selects = val;
      },
    },
    mounted() {
      this.routes = [
        {
          name: '平台管理',
          path: 'PlatformManagement',
        },
      ];
      this.activeIndex = this.routeParams() ? this.routeParams().platform : '0';
      this.getDataList();
    }
  }
</script>

<style scoped>
  .tsp-content {
    width: 95%;
    margin-left: 2.5%;
    margin-top: 2rem;
    background: #ffffff;

  }

  .tsp-pagination {
    padding: 1rem;
  }
  .tsp-ae-item {
    min-width: 48.1875rem;
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
  }
  .tsp-ae-input {
    width: 12rem;
  }
  .tsp-ae-subtitle {
    width: 4rem;
    margin-right: 1rem;
    margin-left: 1.5rem;
  }

</style>



