<template>
  <div class="tsp-cd-mask" :class="{'tsp-mask-isActive':myIsShow}">
    <div class="tsp-cd-main-confirm" v-if="param.type == 'confirm'">
      <div class="tsp-cd-top">
        <img @click="onClose" src="../../assets/close-black.png" alt="" class="tsp-cd-close">
      </div>
      <div class="tsp-cd-content">
        <div class="tsp-cd-title"><b>是否删除？</b></div>
        <div class="tsp-cd-text">删除后不可恢复，是否删除{{param.text}}</div>
        <div class="tsp-cd-bts">
          <div class="tsp-cd-bt tsp-cd-bt-cancel" @click="onCancel">取 消</div>
          <div class="tsp-cd-bt tsp-cd-bt-ok" @click="onOk">确 定</div>
        </div>
      </div>
    </div>
    <div class="tsp-cd-main-strict" v-else>
      <div class="tsp-cd-top">
        <img @click="onClose" src="../../assets/close-white.png" alt="" class="tsp-cd-close">
      </div>
      <div class="tsp-cd-content">
        <div class="tsp-cd-title"><b>是否强制删除？</b></div>
        <div class="tsp-cd-text">所选项下还有数据，是否强制删除{{param.text}}</div>
        <div class="tsp-cd-bts">
          <div class="tsp-cd-bt tsp-cd-bt-cancel" @click="onCancel">取 消</div>
          <div class="tsp-cd-bt tsp-cd-bt-ok" @click="onOk">确 定</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'DeleteDialog',
    data() {
      return {
        myIsShow: this.isShow
      }
    },
    props: ['isShow', 'param'],
    methods: {
      onClose() {
        this.myIsShow = false;
      },
      onOk() {
        this.onClose();
        this.$emit('handleOk');
      },
      onCancel() {
        this.onClose();
        this.$emit('handleCancel');
      },
    },
    computed: {},
    watch: {
      'myIsShow': function (curVal, oldVal) {
        this.$emit('handleShow', curVal);
      },
      isShow(val) {
        this.myIsShow = val;
      }
    },
    mounted() {
    }
  }
</script>
<style scoped>
  .tsp-cd-bt-ok {
    border: 1px solid #ec6a41;
    color: white;
    background: #ec6a41;
  }

  .tsp-cd-bt-cancel {
    border: 1px solid #959595;
    color: white;
    background: #959595;
  }

  .tsp-cd-bt {
    padding: 0.5rem 2rem;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 0.3rem;
    width: fit-content;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none;
  }

  .tsp-cd-title {
    font-size: 1.375rem;
  }

  .tsp-cd-text {
    margin-top: 1rem;
    margin-bottom: 1rem;
    color: #7189a2;
  }

  .tsp-cd-bts {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    padding: 1rem 2rem;
  }

  .tsp-cd-content {
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10.4375rem;
  }

  .tsp-cd-close {
    color: #5f5f5f;
    cursor: pointer;
  }

  .tsp-cd-main-strict {
    width: 25rem;
    height: 23.125rem;
    padding: 1.1875rem;
    margin: 20% auto auto;
    background: url("../../assets/delete_dialog_strict.png") no-repeat;
    background-size: contain;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
  }

  .tsp-cd-main-confirm {
    width: 25rem;
    height: 23.125rem;
    padding: 1.1875rem;
    background: url("../../assets/delete_dialog_bg.png") no-repeat;
    background-size: contain;
    margin: 20% auto auto;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
  }

  .tsp-cd-top {
    display: -webkit-flex;
    display: flex;
    justify-content: flex-end;
  }

  .tsp-cd-mask {
    position: fixed;
    z-index: 2000;
    display: none;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(88, 88, 88, 0.5);
  }

  .tsp-mask-isActive {
    display: block;
  }
</style>



