<template>
  <div class="tsp-content-whole">
    <div class="tsp-aside">
      <div class="tsp-logo">
        <img src="./../assets/logo.png" alt="" class="tsp-title-logo">
      </div>
      <div>
        <el-menu :unique-opened="true" ref="menu"
                 class="tsp-menu"
                 text-color="#a6b1c2"
                 background-color="#313d4a">
          <el-menu-item v-for="(item,index) in menu" :index="index+''" style="padding: 0">
            <div slot="title" class="tsp-menu-item"
                 :class="{'tsp-menu-item-selected':item.isSelected}"
                 @click="onMenuItemClick(item,index)">
              <img :src="item.iconUrl" alt="" class="tsp-icon-location">
              <span class="tsp-menu-title">{{item.title}}</span>
            </div>
          </el-menu-item>
        </el-menu>

      </div>
    </div>
    <div class="tsp-main">
      <div class="tsp-head"></div>
      <router-view class="tsp-c-router-view">
      </router-view>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        user: {
          username: 'Admin'
        },
        menu: [
          {
            title: '首 页',
            iconUrl: require('./../assets/home.png'),
            route: "/",
            isSelected: false
          },
          {
            title: '平台管理',
            iconUrl: require('./../assets/平台.png'),
            route: "/PlatformManagement/PlatformManagement",
            isSelected: false
          },
          {
            title: '账号管理',
            iconUrl: require('./../assets/账号.png'),
            route: "/AccountManagement/AccountManagement",
            isSelected: false
          },
          {
            title: '接入配置',
            iconUrl: require('./../assets/接入.png'),
            route: "/AccessConfig/AccessConfig",
            isSelected: false
          },
          {
            title: '平台检测',
            iconUrl: require('./../assets/平台.png'),
            route: "/PlatformTest/PlatformTest",
            isSelected: false
          },
          {
            title: '车辆检测',
            iconUrl: require('./../assets/车辆检测.png'),
            route: "/VehicleDetectionManagement/VehicleDetectionInfo",
            isSelected: false
          },
          {
            title: '检测监控',
            iconUrl: require('./../assets/检测监控.png'),
            route: "/MonitoringManagement/MonitoringManagement",
            isSelected: false
          },
          {
            title: '接入状态',
            iconUrl: require('./../assets/接入状态.png'),
            route: "/AccessState/AccessState",
            isSelected: false
          },
          {
            title: '数据统计',
            iconUrl: require('./../assets/数据提交.png'),
            route: "/PlatformManagement/PlatformManagement",
            isSelected: false
          },
        ],
        selectMenuItem: {},
        key: 'selectMenuItem'
      }
    },
    methods: {
      onMenuItemClick(obj, index) {
        console.log(obj, index);
        if (obj) {
          sessionStorage.setItem(this.key, index);
          this.selectMenuItem.isSelected = false;
          obj.isSelected = true;
          this.selectMenuItem = obj;
          this.$router.push(obj.route);
        }
      },
      findItem(index) {
        return this.menu[index]
      }
    },
    mounted() {
      //用户之前选中的item
      if (sessionStorage.getItem(this.key)) {
        let index = sessionStorage.getItem(this.key);
        this.onMenuItemClick(this.findItem(index), index);
      }
    }
  }
</script>

<style scoped>
  .tsp-menu-item {
    margin-top: 1rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }

  .tsp-menu-item-selected {
    background: #172932;
    color: #ffffff;
  }

  .tsp-menu {
    border: 0;
    margin-top: 3rem;
    padding: 0;
  }

  .tsp-icon-location {
    height: 1.875rem;
    width: 1.875rem;
    vertical-align: middle;
  }

  .tsp-content-whole {
    display: flex;
    display: -webkit-flex;
  }

  .tsp-main {
    flex-grow: 1;
    overflow: auto;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
  }

  .tsp-head {
    background: #313d4a;
    height: 4rem;
  }

  .tsp-aside {
    width: 15rem;
    background: #313d4a;
    min-height: 100%;
  }

  .tsp-c-router-view {
    flex-grow: 1;
    overflow: auto;
    background: #f7f7f7;
    width: 100%;
  }

  .tsp-logo {
    width: 15rem;
    height: 4rem;
    background: #172932;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
  }

  .tsp-title-logo {
    width: 11.75rem;
    height: 1.875rem;
  }

  .tsp-menu-title {
    font-size: 1.125rem;
    margin-left: 1.875rem;
  }

</style>
<style>
  .tsp-breadcrumb {
    padding: 0.6rem 1rem;
    background: white;
    width: 100%;
    min-height: 4.1875rem;
  }

  .tsp-router-link {
    color: white;
    text-decoration: none;
  }

  .tsp-tabs-bt {
    border: 0.05rem solid #448aca;
    border-radius: 1rem;
    color: #448aca;
    min-width: 4.875rem;
    text-align: center;
    cursor: pointer;
  }

  .tsp-bt {
    height: 1.625rem;
    padding: 0rem 0.8rem;
    font-size: 0.875rem;
    cursor: pointer;
    background: white;
    border-radius: 3rem;
  }

  .tsp-bt-look {
    border: 1px solid #448aca;
    color: #448aca;
  }

  .tsp-bt-edit {
    border: 1px solid #46c7a0;
    color: #46c7a0;
  }

  .tsp-bt-set {
    border: 1px solid #00b7ee;
    color: #00b7ee;
  }

  .tsp-bt-download {
    border: 1px solid #32b16c;
    color: #32b16c;
  }

  .tsp-bt-manage {
    border: 1px solid #556fb5;
    color: #556fb5;
  }

  .tsp-click {
    color: #3a8ee6;
    cursor: pointer;
  }

  .tsp-layout-flex {
    display: -webkit-flex;
    display: flex;
    flex-wrap: wrap
  }

  .tsp-flex {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap
  }

  .tsp-flex-start {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    flex-wrap: wrap
  }

  .tsp-flex-end {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .tsp-flex-vertical {
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
  }

  .tsp-flex-center {
    align-items: center;
  }

  .tsp-stroke {
    border: 1px solid #e6ebf5;
    background-color: #fff;
    /*box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);*/
    border-radius: 0.2rem;
  }

  .tsp-pipe {
    display: block;
    height: 100%;
    min-height: 1.25rem;
    background: #00B8EE;
    width: 0.3125rem;
  }

  .tsp-red-star {
    color: red;
    text-align: center;
    width: 1rem;
    vertical-align: middle;
    display: table-cell;
  }

  .tsp-label {
    text-align: justify;
    text-align-last: justify;
    color: #5f6c87;
  }
</style>




