<template>
  <div class="tsp-flex-vertical">
    <div class="tsp-flex-start tsp-breadcrumb">
      <Back></Back>
      <Breadcrumb :list="routes" style="flex-grow: 1;margin-left: 1rem;"></Breadcrumb>
      <TspButton type="cancel" name="取消" margin="true" @onClick="onCancel"></TspButton>
      <TspButton type="confirm" name="确定" :margin="false" @onClick="onOk"></TspButton>
    </div>
    <div class="tsp-npt-content">
      <div class="tsp-content-main">
        <div class="tsp-flex-start">
          <div class="tsp-pipe"></div>
          <div style="margin-left: 1rem"><b>选择新增车辆</b></div>
        </div>
        <div class="tsp-content">
          <el-table :header-row-style="{background:'#efefef'}" :header-cell-style="{background:'#efefef'}"
                    :data="dataList.resultList" stripe
                    @selection-change="handleSelectionChange" @filter-change="onFilterChange"
                    @sort-change="onSortChange">
            <el-table-column type="selection" header-align="center" align="center" width="80">
            </el-table-column>
            <el-table-column label="序号" type="index" width="100">
            </el-table-column>
            <el-table-column label="VIN码" min-width="150" >
              <template slot-scope="scope">
                <div>
                  {{ scope.row.roleName }}
                </div>
              </template>
            </el-table-column>
            <el-table-column label="ICCID号" min-width="150" >
              <template slot-scope="scope">
                <div>
                  {{ scope.row.roleName }}
                </div>
              </template>
            </el-table-column>
            <el-table-column label="车牌号" min-width="150">
              <template slot-scope="scope">
                <span class="tsp-data-color">{{ scope.row.createUser }}</span>
              </template>
            </el-table-column>
            <el-table-column label="车型" min-width="150">
              <template slot-scope="scope">
                <span class="tsp-data-color">{{ scope.row.createTime }}</span>
              </template>
            </el-table-column>
            <el-table-column label="车款" min-width="150" >
              <template slot-scope="scope">
                <div>
                  {{ scope.row.roleName }}
                </div>
              </template>
            </el-table-column>
            <el-table-column label="车辆状态" min-width="150" >
              <template slot-scope="scope">
                <div>
                  {{ scope.row.roleName }}
                </div>
              </template>
            </el-table-column>
            <el-table-column label="车厂名称" min-width="150" >
              <template slot-scope="scope">
                <div>
                  {{ scope.row.roleName }}
                </div>
              </template>
            </el-table-column>
          </el-table>
          <div class="tsp-flex-end tsp-pagination">
            <el-pagination
              background
              :page-sizes="[10, 20, 50, 100]"
              layout="total, sizes, prev, pager, next, jumper"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-size="pageSize"
              :total="dataList.results">
            </el-pagination>
          </div>
        </div>

        <DeleteDialog :isShow="deleteObj.isShow" @handleShow="(val)=> this.deleteObj.isShow=val" :param="deleteObj"
                      @handleOk='onOk'
                      @handleCancel='onCancel'></DeleteDialog>
      </div>
    </div>
  </div>
</template>

<script>
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb"
  import TspDialog from "@/components/custom/ConfirmDialog"
  import Back from "@/components/custom/Back"

  export default {
    name: 'NewPlatformTest',
    components: {
      TspButton,
      Breadcrumb,
      TspDialog,
      Back
    },
    data() {
      return {
        po: {},
        routes: [],
        keyWord: '',
        index: 0,
        pageSize: 10,
        ascending: null,
        activeIndex: '0',
        dataList: {
          results: 0,
          pageCount: 0,
          pageSize: 10,
          currentPage: 1,
          resultList: [{}]
        },
        selects: [],
        routes: [],
        deleteObj: {
          isShow: false,
          text: '',
          type:
            'confirm',
        },
      }
    },
    methods: {
      handleSelect(key, keyPath) {
        this.activeIndex = keyPath[0];
      },
      onFilterChange(filters) {
        console.log(filters);
        if (filters['loginStatus']) {
          this.loginStatus = filters['loginStatus'][0] ? filters['loginStatus'][0] : '';
        }
        this.getDataList()
      },

      onQuery(row) {
        this.post(this.tspRlGAL, {
          id: row.roleId,
        }, (res) => {
          if (res.data.success) {
            this.sharedAuths.dialogVisible = true;
            this.sharedAuths.authList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onSortChange(sort) {
        console.log(sort);
        this.ascending = sort.order;
        this.getDataList()
      },
      showDialog(type = 'confirm') {
        this.deleteObj.isShow = true;
        if (type == 'confirm') {
          this.deleteObj.type = type;
        } else {
          this.deleteObj.type = type;
        }
      },
      onOk() {

      },
      onCancel() {
        this.$router.go(-1);
      },
      onDeleteBefore() {
        if (this.selects && this.selects.length > 0) {
          this.showDialog();
        } else {
          this.$message({showClose: true, message: '请选择要删除的数据!', type: 'warning'});
        }
      },
      onDelete() {
        let ids = [];
        for (let obj of this.selects) {
          ids.push(obj.id);
        }
        this.post(this.tspQtDQ, {
          ids: ids.toString(),
        }, (res) => {
          if (res.data.success) {
            this.$message({showClose: true, message: '删除成功！', type: 'success'});
            this.getDataList();
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },

      onSearch(words) {
        this.keyWord = words;
        this.index = 0;
        this.getDataList();
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getDataList();
      },
      handleCurrentChange(val) {
        this.index = val - 1;
        this.getDataList()
      },
      getDataList() {
        this.post(this.urlListAccounts, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      handleSelectionChange(val) {
        this.selects = val;
      },
    },
    mounted() {
      this.routes = [
        {
          name: '车辆检测',
          path: 'VehicleDetectionInfo',
        },
        {
          name: '新增',
          path: 'VehicleDetectionAdd',
        },
      ];
    }
  }
</script>

<style scoped>
  .tsp-ae-input {
    width: 16.875rem;
  }

  .tsp-ae-item {
    min-width: 48.1875rem;
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
  }

  .tsp-ae-subtitle {
    width: 5rem;
    margin-right: 2.25rem;
  }

  .tsp-content-main {
    background: white;
    border-radius: 0.5rem;
    padding: 1.25rem;
    min-height: 100%;
    width: 100%;
  }

  .tsp-npt-content {
    width: 100%;
    padding: 1rem;
    flex-grow: 1;
    display: flex;
  }
  .tsp-content {
    margin-top: 2rem;
    width: 100%;
  }
</style>



