<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb" style="background: transparent;">
      <!--<Breadcrumb :list="routes" style="flex-grow: 1"></Breadcrumb>-->
      <b id="tspttt">账号管理</b>
    </div>
    <div class="tsp-flex-start" style="background: white;padding: 0 1rem;">
      <Tabs :list="tabList" :defaultValue="activeIndex" @onChange="onTabChange" style="flex-grow: 1;"></Tabs>
      <SearchBox placeholder="账号" v-on:onSearch="onSearch" :maxlength="10"></SearchBox>
      <router-link class="tsp-router-link"
                   :to="{name:'NewAccount',params: { storeParam: {platform:activeIndex} } }">
        <TspButton type="add" name="添加" margin="true"></TspButton>
      </router-link>
      <TspButton type="delete" name="删除" :margin="false" @onClick="onDeleteBefore"></TspButton>
    </div>
    <div class="tsp-content">
      <el-table :header-row-style="{background:'#efefef'}" :header-cell-style="{background:'#efefef'}"
                :data="dataList.resultList" stripe
                @selection-change="handleSelectionChange" @filter-change="onFilterChange"
                @sort-change="onSortChange">
        <el-table-column type="selection" header-align="center" align="center" width="80">
        </el-table-column>
        <el-table-column label="序号" type="index" width="100">
        </el-table-column>
        <el-table-column label="车厂名称" min-width="150" filter-placement="bottom" :filter-multiple="false"
                         prop="statusName"
                         :filters="[{text: '禁用', value: '0'}, {text: '启用', value:'1'}]" column-key="status">
          <template slot-scope="scope">
            <div>
              {{scope.row.name }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="检测平台" min-width="180" filter-placement="bottom" :filter-multiple="false"
                         prop="statusName2"
                         :filters="[{text: '禁用', value: '0'}, {text: '启用', value:'1'}]" column-key="status2">
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="账号" min-width="180">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createUser }}</span>
          </template>
        </el-table-column>
        <el-table-column label="密码" min-width="180">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" header-align="center" align='center' width="100">
          <template slot-scope="scope">
            <router-link class="tsp-router-link"
                         :to="{name:'AccountEdit' ,params: { storeParam: {platform:activeIndex} } }">
              <div class="tsp-bt tsp-bt-edit">编 辑</div>
            </router-link>
          </template>
        </el-table-column>
      </el-table>
      <div class="tsp-flex-end tsp-pagination">
        <el-pagination
          background
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          :total="dataList.results">
        </el-pagination>
      </div>
    </div>

    <DeleteDialog :isShow="deleteObj.isShow" @handleShow="(val)=> this.deleteObj.isShow=val" :param="deleteObj"
                  @handleOk='onOk'
                  @handleCancel='onCancel'></DeleteDialog>
  </div>
</template>

<script>
  import Tabs from "@/components/custom/Tabs.vue"
  import SearchBox from "@/components/custom/SearchBox"
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb.vue"
  import DeleteDialog from "@/components/custom/DeleteDialog.vue"
  import TspDialog from "@/components/custom/ConfirmDialog.vue"

  export default {
    name: 'AccountManagement',
    components: {
      SearchBox,
      TspButton,
      Breadcrumb,
      DeleteDialog,
      TspDialog,
      Tabs
    },
    data() {
      return {
        keyWord: '',
        index: 0,
        pageSize: 10,
        ascending: null,
        activeIndex: 0,
        tabList: [{name: '接入账号'}, {name: '检测账号'}],
        dataList: {
          results: 0,
          pageCount: 0,
          pageSize: 10,
          currentPage: 1,
          resultList: [{}]
        },
        selects: [],
        routes: [],
        deleteObj: {
          isShow: false,
          text: '',
          type:
            'confirm',
        },
      }
    },
    methods: {
      onTabChange(item, index) {
        this.activeIndex = index;
      },
      onFilterChange(filters) {
        console.log(filters);
        if (filters['loginStatus']) {
          this.loginStatus = filters['loginStatus'][0] ? filters['loginStatus'][0] : '';
        }
        this.getDataList()
      },

      onQuery(row) {
        this.post(this.tspRlGAL, {
          id: row.roleId,
        }, (res) => {
          if (res.data.success) {
            this.sharedAuths.dialogVisible = true;
            this.sharedAuths.authList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onSortChange(sort) {
        console.log(sort);
        this.ascending = sort.order;
        this.getDataList()
      },
      showDialog(type = 'confirm') {
        this.deleteObj.isShow = true;
        if (type == 'confirm') {
          this.deleteObj.type = type;
        } else {
          this.deleteObj.type = type;
        }
      },
      onOk() {
        this.onDelete();
      },
      onCancel() {

      },
      onDeleteBefore() {
        if (this.selects && this.selects.length > 0) {
          this.showDialog();
        } else {
          this.$message({showClose: true, message: '请选择要删除的数据!', type: 'warning'});
        }
      },
      onDelete() {
        let ids = [];
        for (let obj of this.selects) {
          ids.push(obj.id);
        }
        this.post(this.tspQtDQ, {
          ids: ids.toString(),
        }, (res) => {
          if (res.data.success) {
            this.$message({showClose: true, message: '删除成功！', type: 'success'});
            this.getDataList();
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },

      onSearch(words) {
        this.keyWord = words;
        this.index = 0;
        this.getDataList();
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getDataList();
      },
      handleCurrentChange(val) {
        this.index = val - 1;
        this.getDataList()
      },
      getDataList() {
        this.post(this.urlAccountList, {
          pageIndex: this.index,
          limit: this.pageSize,
          word: this.keyWord,
          type: this.activeIndex + 1,
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      handleSelectionChange(val) {
        this.selects = val;
      },
    },
    mounted() {
      this.routes = [
        {
          name: '账号管理',
          path: 'PlatformManagement',
        },
      ];
      this.activeIndex = this.routeParams() ? this.routeParams().platform : 0;
      this.getDataList();
      console.log('scrollTo', document.getElementById('tspttt').offsetHeight);
    }
  }
</script>

<style scoped>
  .tsp-content {
    width: 100%;
  }

  .tsp-pagination {
    padding: 1rem;
  }
</style>



