<template>
  <div class="tsp-input-mark" :style="cellStyle">
    <div class="tsp-input-main">
      <div class="tsp-input-main-line">
        <slot name="before"></slot>
        <div class="tsp-input-area">
          <input v-model="word" :type="type" class="tsp-input-input" :placeholder="placeholder" :maxlength="maxlength"
                 :style="state.style"
                 @focus="onFocus" @blur="onBlur" spellcheck="false" @keyup="onKeyup($event)">
          <img class="tsp-input-clear" src="../../assets/close-white.png" @click="onClear" v-show="word">
        </div>
        <slot name="after"></slot>
      </div>
      <img src="../../assets/cue.png" alt="" class="tsp-input-message-icon" v-show="state.level=='err'">
    </div>
    <div class="tsp-input-message" v-if="state.message&&(state.message!=='')">{{state.message}}</div>
  </div>
</template>

<script>
  export default {
    name: 'Input',
    data() {
      return {
        word: '',
        state: {
          level: '',
          message: '',
          style: {}
        },
        isFocus: false
      }
    },
    model: {
      prop: 'words',
    },
    props: {
      placeholder: String,
      words: String,
      maxlength: Number,
      filter: Function,
      mark: String,
      clearable: {
        type: Boolean,
        default: false
      },
      cellStyle: Object,
      type: {
        type: String,
        default: 'text'
      }
    },
    methods: {
      onClear() {
        this.word = '';
      },
      onKeyup(event) {
        this.$emit('onKeyup', event, this.words);
      },
      onFocus() {
        this.isFocus = true;
        this.$emit('onFocus', this.words);
      },
      onBlur() {
        this.isFocus = false;
        this.$emit('onBlur', this.words);
      },
      setState(state) {
        this.state = state;
      }
    },
    watch: {
      word(newVal, oldVal) {
        try {
          if (typeof this.filter === "function") {
            newVal = this.filter(newVal, this.mark, this.setState);
            this.word = newVal;
          }
          this.$emit('input', newVal);
        } catch (e) {
          console.log(e);
        }
      }
    },
    mounted() {
      this.word = this.words;
    },
  }
</script>
<style scoped>
  * {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }

  .tsp-input-mark {
    color: #333333;
  }

  .tsp-input-input {
    border: 0.08rem solid #d8dce5;
    padding: 0.6rem 2rem 0.6rem 0.8rem;
    border-radius: 0.3rem;
    outline: none;
    width: 100%;
    height: 100%;
  }

  .tsp-input-input:focus {
    border: 0.08rem solid #409EFF;
  }

  .tsp-input-main {
    height: 100%;
    width: 100%;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    flex-wrap: nowrap;
    position: relative;
  }

  .tsp-input-main-line {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    flex-wrap: nowrap;
    flex-grow: 1;
    height: 100%;
  }

  .tsp-input-message {
    color: #ED6942;
    font-size: 0.8rem;
    margin-top: 0.1rem;
  }

  .tsp-input-message-icon {
    height: 1.375rem;
    width: 1.375rem;
    position: absolute;
    right: -1.8rem;
  }

  .tsp-input-area {
    height: 100%;
    flex-grow: 1;
    width: inherit;
    position: relative;
    display: flex;
    display: -webkit-flex;
    align-items: center;
  }

  .tsp-input-clear {
    background: grey;
    border-radius: 3rem;
    position: absolute;
    padding: 0.2rem;
    right: 1rem;
    height: 1rem;
    width: 1rem;
    color: white;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    text-align: justify;
  }

</style>



