<template>
  <div class="tsp-bc-main">
    <div v-for="(item,index) in list" @click="onClick(item,index)"
         :class="{'tsp-bc-turn':index<list.length-1}" class="tsp-bc-crumb">
      <div :class="{'tsp-bc-name':index<list.length-1}">{{item.name}}</div>
      <div v-if="index<list.length-1" class="tsp-bc-arrow">></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Breadcrumb',
    data() {
      return {}
    },
    props: ['list', 'isNeedParam'],
    methods: {
      onClick(item, index) {
        if (index < this.list.length - 1) {
          let route = {
            name: item.path
          };
          if (this.isNeedParam) {
            route.params = item;
          }
          this.$router.push(route);
        }
      },
    },
    mounted() {
    }
  }
</script>
<style scoped>
  .tsp-bc-main {
    display: -webkit-flex;
    display: flex;
    width: fit-content;
    font-size: 0.875rem;
    color: #9fa4ad;
  }

  .tsp-bc-crumb {
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .tsp-bc-name {
    cursor: pointer;
    color: black;
  }

  .tsp-bc-turn {
    font-weight: bold
  }

  .tsp-bc-arrow {
    padding: 0.5rem;
  }
</style>



