<template>
  <div class="tsp-message">
    <div class="tsp-message-main" @click="onClick">
      <div class="tsp-message-menu">
        <img @click="onClose" src="../../../../assets/close-black.png" alt="" class="tsp-message-close"
             v-if="showClose">
      </div>
      <div v-if="type=='error'" class="tsp-message-error">
        <div class="tsp-message-title" style="margin-top: 0.3125rem;"><b>提示</b></div>
        <img src="../../../../assets/message_error_bg.png" alt=""
             style="width: 10.6875rem;height: 7.75rem;margin-top: 1.875rem;">
        <div class="tsp-message-content-error-exp" v-html="message" v-if="errExp"></div>
        <div class="tsp-message-content-error" v-html="message" v-else></div>
        <div class="tsp-message-bt-detail" @click="errExp=false" v-show="errExp">收起</div>
        <div class="tsp-message-bt-detail" @click="errExp=true" v-show="(!errExp) && (message.length>48)">详细信息</div>
      </div>
      <div v-else class="tsp-message-default">
        <img src="../../../../assets/message_bg.png" alt=""
             style="width: 12.8125rem;height: 9.25rem;margin-top: 1.3125rem;">
        <div class="tsp-message-title"><b>提示</b></div>
        <div class="tsp-message-content" v-html="message"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: 'message',
    data() {
      return {
        message: '',
        duration: 3000,
        showClose: true,
        type: '',
        errExp: false
      }
    },
    mounted() {
    },
    methods: {
      onClick(event) {
      },
      onClose() {
        document.body.removeChild(this.$el);
      }
    }
  }
</script>
<style scoped>
  .tsp-message-close {
    height: 1rem;
    width: 1rem;
  }

  .tsp-message-menu {
    display: flex;
    display: -webkit-flex;
    justify-content: flex-end;
    width: 100%;
    cursor: pointer;
    height: fit-content;
  }

  .tsp-message-title {
    margin-top: 2rem;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    font-size: 1.25rem;
  }

  .tsp-message-content {
    margin-top: 1.375rem;
    padding-left: 1rem;
    padding-right: 1rem;
    width: 100%;
    height: 4rem;
    color: #7189a2;
    overflow-x: hidden;
    overflow-y: auto;
    text-align: center;
  }

  .tsp-message-main {
    width: 25rem;
    background: white;
    border-radius: 0.5rem;
    min-height: 20rem;
    padding: 1.1875rem;
    box-sizing: border-box;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
    transition: opacity 1s, transform 1s, left 1s, right 1s, top 1s, bottom 1s;
    overflow: hidden;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    flex-direction: column;
    pointer-events: all;
  }

  .tsp-message {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    z-index: 20000;
    pointer-events: none;
  }

  .tsp-message-default {
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .tsp-message-error {
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .tsp-message-bt-detail {
    border: 1px solid #1ca8eb;
    color: #1ca8eb;
    border-radius: 0.3rem;
    padding: 0.2rem 1rem;
    width: fit-content;
    cursor: pointer;
  }

  .tsp-message-content-error {
    text-align: left;
    margin-bottom: 1.25rem;
    word-break: break-all;
    color: #7189a2;
    text-overflow: ellipsis;
    display: -webkit-box; /** 将对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp: 2; /** 显示的行数 **/
    overflow: hidden; /** 隐藏超出的内容 **/
    margin-top: 1.375rem;
  }

  .tsp-message-content-error-exp {
    text-align: left;
    margin-bottom: 1.25rem;
    color: #7189a2;
    margin-top: 1.375rem;
  }
</style>



