<template>
  <div class="tsp-mark">
    <input v-model="word" type="text" class="tsp-input" :placeholder="placeholder" @keyup.enter="onSearch"
           :maxlength="maxlength" @focus="onFocus" @blur="onBlur" spellcheck="false" @keyup="onKeyup($event)">
    <img :src="icon" alt="" @click="onSearch" class="tsp-search-icon">
  </div>
</template>

<script>
  export default {
    name: 'SearchBox',
    data() {
      return {
        word: '',
        icon: ''
      }
    },
    model: {
      prop: 'words',
      event: 'listenWords'
    },
    props: ['placeholder', 'words', 'maxlength'],
    methods: {
      onKeyup(event) {
        this.$emit('onKeyup', event, this.word);
      },
      onSearch() {
        this.$emit('onSearch', this.word);
      },
      onFocus() {
        this.icon = require('../../assets/seach_active.png')
      },
      onBlur() {
        this.icon = require('../../assets/seach_icon.png')
      }
    },
    mounted() {
      this.$emit('listenWords', this.word);
      this.icon = require('../../assets/seach_icon.png')
    }
  }
</script>
<style scoped>
  * {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }

  .tsp-search-icon {
    display: block;
    position: absolute;
    top: 0.5rem;
    left: 0.8rem;
    height: 1rem;
    width: 1rem;
    z-index: 3;
  }

  .tsp-search-icon:focus {
  }

  .tsp-search-icon:active {
  }

  .tsp-input {
    border: 0.08rem solid #bfbfbf;
    height: 2rem;
    border-radius: 3rem;
    padding-left: 2.3rem;
    padding-right: 0.5rem;
    outline: none
  }

  .tsp-input:focus {
    border: 0.08rem solid #bfbfbf;
  }

  .tsp-mark {
    position: relative;
    font-size: 0.875rem;
    color: #333333;

  }
</style>



