import Vue from 'vue'
import Router from 'vue-router'
import Content from '@/components/Content'
import Login from '@/components/UserSystem/Login/Login'
import PlatformManagement from '@/components/PlatformManagement/PlatformManagement/PlatformManagement'
import PlatformAdd from '@/components/PlatformManagement/PlatformAdd/PlatformAdd'
import PlatformEdit from '@/components/PlatformManagement/PlatformEdit/PlatformEdit'
import AccountManagement from '@/components/AccountManagement/AccountManagement/AccountManagement'
import AccountEdit from '@/components/AccountManagement/AccountEdit/AccountEdit'
import NewAccount from '@/components/AccountManagement/NewAccount/NewAccount'
import PlatformTest from '@/components/PlatformTest/PlatformTest/PlatformTest'
import NewPlatformTest from '@/components/PlatformTest/NewPlatformTest/NewPlatformTest'
import AccessConfig from '@/components/AccessConfig/AccessConfig/AccessConfig'
import VehicleDetectionInfo from '@/components/VehicleDetectionManagement/VehicleDetectionInfo/VehicleDetectionInfo'
import VehicleDetectionAdd from '@/components/VehicleDetectionManagement/VehicleDetectionAdd/VehicleDetectionAdd'
import MonitoringManagement from '@/components/MonitoringManagement/MonitoringManagement/MonitoringManagement'
import AccessState from '@/components/AccessState/AccessState/AccessState'
import Home from '@/components/Home/Home/Home'


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'content',
      component: Content,
      redirect: "/Home",
      children: [
        {
          path: '/Home',
          name: 'Home',
          component: Home,
        },
        {
          path: '/PlatformManagement/PlatformManagement',
          name: 'PlatformManagement',
          component: PlatformManagement,
        },
        {
          path: '/PlatformManagement/PlatformAdd',
          name: 'PlatformAdd',
          component: PlatformAdd,
        },
        {
          path: '/PlatformManagement/PlatformEdit',
          name: 'PlatformEdit',
          component: PlatformEdit,
        },
        {
          path: '/AccountManagement/AccountManagement',
          name: 'AccountManagement',
          component: AccountManagement,
        },
        {
          path: '/AccountManagement/AccountEdit',
          name: 'AccountEdit',
          component: AccountEdit,
        },
        {
          path: '/AccountManagement/NewAccount',
          name: 'NewAccount',
          component: NewAccount,
        },
        {
          path: '/PlatformTest/PlatformTest',
          name: 'PlatformTest',
          component: PlatformTest,
        },
        {
          path: '/PlatformTest/NewPlatformTest',
          name: 'NewPlatformTest',
          component: NewPlatformTest,
        },
        {
          path: '/AccessConfig/AccessConfig',
          name: 'AccessConfig',
          component: AccessConfig,
        },
        {
          path: '/VehicleDetectionManagement/VehicleDetectionInfo',
          name: 'VehicleDetectionInfo',
          component: VehicleDetectionInfo,
        },
        {
          path: '/VehicleDetectionManagement/VehicleDetectionAdd',
          name: 'VehicleDetectionAdd',
          component: VehicleDetectionAdd,
        },
        {
          path: '/MonitoringManagement/MonitoringManagement',
          name: 'MonitoringManagement',
          component: MonitoringManagement,
        },
        {
          path: '/AccessState/AccessState',
          name: 'AccessState',
          component: AccessState,
        },
      ]
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login,
    },
  ]
})



