<template>
  <div class="tsp-button-main" @click="onClick" :class="{'tsp-button-margin':margin}">
    <!--<el-popover v-if="type == 'delete'"-->
    <!--ref="deletePop"-->
    <!--placement="top"-->
    <!--v-model="visiblePop">-->
    <!--<p>确定删除吗？</p>-->
    <!--<div>-->
    <!--<el-button type="primary" size="mini" @click="onCancel">取消</el-button>-->
    <!--<el-button type="warning" size="mini" @click="onConfirm">确定</el-button>-->
    <!--</div>-->
    <!--</el-popover>-->
    <slot>
      <div class="tsp-mask"
           :class="{'tsp-button-add':type=='add','tsp-button-delete':type=='delete','tsp-button-commit':type=='commit',
           'tsp-button-confirm':type=='confirm','tsp-button-cancel':type=='cancel','tsp-button-edit':type=='edit','tsp-button-publish':type=='publish','tsp-button-more':type=='more',
           'tsp-button-export' :type=='export', 'tsp-button-refresh' :type=='refresh', 'tsp-button-search' :type=='search'}">
        <img :src="icon" alt="" class="tsp-button-icon" v-if="showIcon">
        <div class="tsp-button-text">{{name}}</div>
      </div>

    </slot>
  </div>
</template>

<script>
  export default {
    name: 'Button',
    data() {
      return {
        visiblePop: false,
        icon: '',
      }
    },
    props: {
      name: {
        type: String,
        default: function () {
          return ''
        }
      },
      type: {
        type: String,
        default: function () {
          return ''
        }
      },
      margin: {
        type: null,
        default: function () {
          return false
        }
      },
      showIcon: {
        type: Boolean,
        default: function () {
          return true
        }
      },
    },
    methods: {
      onCancel() {
        this.visiblePop = false;
      },
      onConfirm() {
        this.visiblePop = false;
        this.$emit('onClick');
      },
      onClick() {
        if (this.type == 'delete') {
          this.visiblePop = true;
        } else {
          this.visiblePop = false;
        }
        this.$emit('onClick');
      },
    },
    mounted() {
      if (this.type == 'delete') {
        this.icon = require('../../assets/delete.png');
      } else if (this.type == 'add') {
        this.icon = require('../../assets/add.png');
      } else if (this.type == 'edit') {
        this.icon = require('../../assets/edit.png');
      } else if (this.type == 'confirm') {
        this.icon = require('../../assets/confirm.png');
      } else if (this.type == 'publish') {
        this.icon = require('../../assets/release.png');
      } else if (this.type == 'cancel') {
        this.icon = require('../../assets/cancel.png');
      }else if (this.type == 'export') {
        this.icon = require('../../assets/export.png');
      }else if (this.type == 'refresh') {
        this.icon = require('../../assets/refresh.png');
      }else if (this.type == 'search') {
        this.icon = require('../../assets/search.png');
      }
    }
  }
</script>
<style scoped>
  .tsp-button-main {
    display: inline-block;
    *display: inline;
    cursor: pointer;
    background: transparent;
  }

  .tsp-mask {
    height: 2rem;
    padding: 0 1rem;
    font-size: 1rem;
    color: white;
    border-radius: 3rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    white-space: nowrap;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
  }

  .tsp-button-text {
    margin-top: -0.1rem;
  }

  .tsp-button-icon {
    height: 1.25rem;
    width: 1.25rem;
    margin-right: 0.5rem;
    display: block;
  }

  .tsp-button-margin {
    margin: 0.5rem 0.5rem;
  }

  .tsp-button-add {
    background: #00b7ee;
  }

  .tsp-button-add:active {
    background: #00c7cc;
  }

  .tsp-button-delete {
    background: #ec6941;
  }

  .tsp-button-delete:active {
    background: #dc5931;
  }

  .tsp-button-commit {
    background: #46c89f;
  }

  .tsp-button-commit:active {
    background: #36b88f;
  }

  .tsp-button-edit {
    background: #46c7a0;
  }

  .tsp-button-edit:active {
    background: #36b790;
  }

  .tsp-button-confirm {
    background: #32b16c;
  }

  .tsp-button-confirm:active {
    background: #22a15c;
  }

  .tsp-button-cancel {
    background: #556fb5;
  }

  .tsp-button-cancel:active {
    background: #455fa5;
  }

  .tsp-button-publish {
    background: #448aca;
  }

  .tsp-button-publish:active {
    background: #347aba;
  }
  .tsp-button-more {
    background: #46c7a0;
    width: 6rem;
  }

  .tsp-button-more:active {
    background: #36b790;
  }
  .tsp-button-more:active {
    background: #36b790;
  }
  .tsp-button-export {
    background: #13b5b1;
  }
  .tsp-button-refresh{
    background: #00b7ee;
  }
  .tsp-button-search {
    background: #7eb840;
  }
</style>



