<template>
  <el-select v-model="select" :placeholder="placeholder" @change="onChange">
    <el-option :label="option[prop.label]?option[prop.label]:''" :value="option[prop.value]"
               v-for="option in options"></el-option>
  </el-select>
</template>

<script>
  export default {
    name: 'BaseInfoSelect',
    data() {
      return {
        options: [],
        select: ''
      }
    },
    props: {
      prop: {
        type: Object,
        default: function () {
          return {
            label: 'name',
            value: 'value',
            result: 'value'
          }
        }
      },
      url: {
        type: String,
        default: function () {
          return ''
        }
      },
      params: {
        type: Object,
        default: function () {
          return new Object()
        }
      },
      defaultValue: {
        type: String,
        default: function () {
          return ''
        }
      },
      placeholder: {
        type: String,
        default: function () {
          return '请选择'
        }
      },
    },
    methods: {
      onChange(val) {
        if (this.prop.result == 'option') {
          for (let obj of this.options) {
            if (obj[this.prop.value] == val) {
              this.$emit('onChange', obj);
              return;
            }
          }
        } else {
          this.$emit('onChange', val);
        }
      },
      getOptions() {
        this.select = this.defaultValue;
        this.$http.post(this.url, this.params, {
          emulateJSON: true,
          headers: {
            Authorization: 'Bearer ' + (JSON.parse(this.localStore(this.keyToken)) ? JSON.parse(this.localStore(this.keyToken))['access_token'] : ""),
          }
        }).then(function (res) {
          console.log(res);
          if (res.data.success) {
            this.options = res.data.data;
            this.$emit('onHttpRes', res.data);
          } else {
          }
        }, function (res) {
        });
      }
    },
    watch: {
      defaultValue: function (curVal, oldVal) {
        this.select = curVal;
      },
      params: {
        handler: function (curVal, oldVal) {
          this.getOptions();
        },
        deep: true,
        immediate: true
      }
    },
    mounted() {
      this.select = this.defaultValue;
    }
  }
</script>
<style scoped>
</style>



