<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb">
      <Breadcrumb :list="routes" style="flex-grow: 1"></Breadcrumb>
      <div v-show="!isEditing">
        <TspButton type="edit" name="编辑" margin="true" @onClick="isEditing=true"></TspButton>
      </div>
      <div v-show="isEditing">
        <TspButton type="cancel" name="取消" margin="true" @onClick="isEditing=false"></TspButton>
        <TspButton type="confirm" name="确定" :margin="false" ></TspButton>
      </div>
    </div>
    <div class="tsp-content ">
      <div class="tsp-content-main">
        <div class="tsp-flex-start">
          <div class="tsp-pipe"></div>
          <div style="margin-left: 1rem"><b>{{activeIndex == '0' ? '接入平台' : '检测平台'}}详情</b></div>
        </div>
        <div class="tsp-layout-flex" style="margin-top: 1rem">
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">平台名称</div>
            <el-input v-model="po.account" clearable v-if="isEditing" class="tsp-ae-input"></el-input>
            <span v-else>丨{{po.account}}</span>
          </div>
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">类型</div>
            <BaseInfoSelect :url="tspOgGQL" class="tsp-ae-input" v-if="isEditing" clearable
                            @onChange="handleRoleChange" :defaultValue="po.platform"
                            :prop="{label:'name',value:'id',result:'option'}"
            ></BaseInfoSelect>
            <span v-else>丨{{po.platform}}</span>
          </div>
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">IP地址</div>
            <el-input v-model="po.account" clearable v-if="isEditing" class="tsp-ae-input"></el-input>
            <span v-else>丨{{po.account}}</span>
          </div>
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">状态</div>
            <BaseInfoSelect :url="tspOgGQL" class="tsp-ae-input" v-if="isEditing" clearable
                            @onChange="handleRoleChange" :defaultValue="po.platform"
                            :prop="{label:'name',value:'id',result:'option'}"
            ></BaseInfoSelect>
            <span v-else>丨{{po.platform}}</span>
          </div>
          <div class="tsp-flex-start tsp-ae-item">
            <div class="tsp-red-star">*</div>
            <div class="tsp-label tsp-ae-subtitle">端口</div>
            <el-input v-model="po.pwd" clearable v-if="isEditing" class="tsp-ae-input"></el-input>
            <span v-else>丨{{po.pwd}}</span>
          </div>
        </div>
        <div class="tsp-flex-start tsp-ae-item" style="align-items: flex-start">
          <div class="tsp-red-star">*</div>
          <div class="tsp-label tsp-ae-subtitle">选点</div>
          <baidu-map class="tsp-map" :center="map.center" :zoom="map.zoom" :map-click="true" :scroll-wheel-zoom="true"
                     @click="onMapClick">
            <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
            <bm-marker :position="markerPoint" v-if="markerPoint">
            </bm-marker>
          </baidu-map>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb"
  import TspDialog from "@/components/custom/ConfirmDialog"
  import BaseInfoSelect from "@/components/custom/BaseInfoSelect.vue"

  export default {
    name: 'AccountEdit',
    components: {
      TspButton,
      Breadcrumb,
      TspDialog,
     BaseInfoSelect
    },
    data() {
      return {
        po: {},
        map: {
          center: {lat: 30, lng: 118},
          zoom: 5
        },
        markerPoint: {},
        routes: [],
        activeIndex: 0,
        isEditing: false
      }
    },
    methods: {
      handleRoleChange(value) {
        console.log(value)
        this.po.platform = value.id;

      },
      onMapClick(event) {
        if (this.isEdit) {
          this.markerPoint = event.point;
        }
      },
      onOk() {
        this.post(this.urlEditAccount, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.onCancel();
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onCancel() {
        this.$router.push({name: 'PlatformManagement', params: {storeParam: {platform: this.activeIndex}}});
      },
      getDataList() {
        this.post(this.urlListAccounts, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
    },

      mounted() {
      this.activeIndex = this.routeParams().platform;
      this.routes = [
        {
          name: '平台管理',
          path: 'PlatformManagement',
        },
        {
          name: this.activeIndex == '0' ? '接入平台' : '检测平台',
          path: 'PlatformManagement',
        },
        {
          name: '编辑',
          path: 'AccountManagement',
        },
      ];
    }
  }
</script>

<style scoped>
  .tsp-map {
    height: 30rem;
    flex-grow: 1;
    border-radius: 0.5rem;
  }

  .tsp-ae-input {
    width: 16.875rem;
  }

  .tsp-ae-item {
    min-width: 48.1875rem;
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
  }

  .tsp-ae-subtitle {
    width: 5rem;
    margin-right: 2.25rem;
  }

  .tsp-content-main {
    background: white;
    border-radius: 0.5rem;
    padding: 1.25rem;
  }

  .tsp-content {
    width: 100%;
    padding: 1rem;
  }
</style>



