<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb">
      <b style="flex-grow: 1">接入状态</b>
      <!--<Breadcrumb :list="routes" style="flex-grow: 1"></Breadcrumb>-->
      <div>
        <TspButton type="refresh" name="刷新" margin="true" @onClick=""></TspButton>
      </div>
    </div>
    <div class="tsp-content ">
      <div class="tsp-content-main">
        <div class="tsp-flex-start">
          <div class="tsp-pipe"></div>
          <div style="margin-left: 1rem"><b>接入状态实时监控</b></div>
          <div class="tsp-img">
              <div class="tsp-img-top">
                <div class="tsp-img-left"> <img :src="Localanomaly" ></div>
                <div class="tsp-img-right"><b >地方平台异常</b></div>
              </div>
            <div class="tsp-img-top" >
              <div class="tsp-img-left"> <img :src="Localnormal" ></div>
              <div class="tsp-img-right"><b >地方平台正常</b></div>
            </div>
            <div class="tsp-img-top">
              <div class="tsp-img-left"> <img :src="Nationalabnormality" ></div>
              <div class="tsp-img-right"><b >国家平台异常</b></div>
            </div>
            <div class="tsp-img-top">
              <div class="tsp-img-left"> <img :src="Normalstate" ></div>
              <div class="tsp-img-right"><b >国家平台正常</b></div>
            </div>
          </div>
        </div>
        <div class="tsp-flex-start tsp-ae-item" style="align-items: flex-start">
          <baidu-map class="tsp-map" :center="map.center" :zoom="map.zoom" :map-click="true" :scroll-wheel-zoom="true"
                     @click="onMapClick">
            <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
            <bm-marker :position="markerPoint" v-if="markerPoint">
            </bm-marker>
          </baidu-map>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb"
  import TspDialog from "@/components/custom/ConfirmDialog"
  import BaseInfoSelect from "@/components/custom/BaseInfoSelect.vue"
  import Localanomaly from "@/assets/Localanomaly.png"
  import Localnormal from "@/assets/Localnormal.png"
  import Nationalabnormality from "@/assets/Nationalabnormality.png"
  import Normalstate from "@/assets/Normalstate.png"

  export default {
    name: 'AccountEdit',
    components: {
      TspButton,
      Breadcrumb,
      TspDialog,
      BaseInfoSelect,
    },
    data() {
      return {
        po: {},
        map: {
          center: {lat: 30, lng: 118},
          zoom: 5
        },
        markerPoint: {},
        routes: [],
        activeIndex: 0,
        Localanomaly:Localanomaly,
        Localnormal:Localnormal,
        Nationalabnormality:Nationalabnormality,
        Normalstate:Normalstate
      }
    },
    methods: {
      handleRoleChange(value) {
        console.log(value)
        this.po.platform = value.id;

      },
      onMapClick(event) {
        if (this.isEdit) {
          this.markerPoint = event.point;
        }
      },
      onOk() {
        this.post(this.urlEditAccount, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.onCancel();
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onCancel() {
        this.$router.push({name: 'PlatformManagement', params: {storeParam: {platform: this.activeIndex}}});
      },
      getDataList() {
        this.post(this.urlListAccounts, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
    },

    mounted() {
      this.routes = [
        {
          name: '接入状态',
          path: 'AccessState',
        },
      ];
    }
  }
</script>

<style scoped>
  .tsp-map {
    height: 40rem;
    flex-grow: 1;
    border-radius: 0.5rem;
  }

  .tsp-ae-input {
    width: 16.875rem;
  }

  .tsp-ae-item {
    min-width: 48.1875rem;
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    height: 40rem;
  }

  .tsp-ae-subtitle {
    width: 5rem;
    margin-right: 2.25rem;
  }

  .tsp-content-main {
    background: white;
    border-radius: 0.5rem;
    padding: 1.25rem;
  }

  .tsp-content {
    width: 100%;
    padding: 1rem;
  }
  .tsp-img{
    margin-left: 50rem;

  }
  .tsp-img-top{
    margin-left: 1rem;
    width: 9rem;
    height:2rem;
    float: left;
  }
  .tsp-img-left{

  }
  .tsp-img-right{
    float: right;
    margin-top: -1.75rem;
  }
</style>



