<template>
  <div>
    <img :src="require('../../assets/back.png')" alt="" style="height: 1.625rem;width: 1.625rem;cursor: pointer;"
         @click="onBack">
  </div>
</template>

<script>
  export default {
    name: 'Back',
    data() {
      return {}
    },
    methods: {
      onBack() {
        this.$router.go(-1);
      },
    },
  }
</script>
<style scoped>
</style>



