// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router/index'
import elementui from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueResource from "vue-resource";
import Vuex from 'vuex'
import BaiduMap from 'vue-baidu-map'
import Util from '@/components/Util.js'
import Message from '@/components/custom/message'
import App from './App'

Vue.use(elementui);
Vue.use(VueResource);
Vue.use(Vuex);
Vue.use(Util);
Vue.use(Message);

Vue.config.productionTip = false;
Vue.use(BaiduMap, {
  ak: 'wG2epS5iEC4kG1rdFGe1a9wu'
});

const store = new Vuex.Store({
  state: {
    token: '',
    routeMemory: {},
    treeCurrentChecked: null,
    selectMenuItem: null,
    userInfo: {
      treeOrz: null,
    },
  },
  mutations: {
    saveToken(state, token) {
      state.token = token;
    },
    saveRouteMemory(state, memory) {
      state.routeMemory[memory.path] = memory.content;
    },
    saveTreeCurrentChecked(state, node) {
      state.treeCurrentChecked = node;
    },
    saveSelectMenuItem(state, node) {
      state.selectMenuItem = node;
    }
  },
  getters: {
    queryRouteMemory(state) {
      return state.routeMemory
    },
    queryTreeCurrentChecked(state) {
      return state.treeCurrentChecked
    },
    querySelectMenuItem(state) {
      return state.selectMenuItem
    },
    queryToken(state) {
      return state.token
    }
  }
});

Vue.http.interceptors.push((request, next) => {
  next((response) => {
    return response;
  });
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App},
});



