<template>
  <div>
    <div class="tsp-flex-start tsp-breadcrumb" style="background: transparent;">
      <b style="flex-grow: 1;">车辆检测</b>
        <div class="tsp-bt tsp-nu-bt-more" style="background: #448bca">配置平台</div>
        <div class="tsp-bt tsp-nu-bt-more" style="background: #00a869">平台登录</div>
        <div class="tsp-bt tsp-nu-bt-more" style="background: #5470b4">平台登出</div>
      <router-link class="tsp-router-link"
                   :to="{name:'VehicleDetectionAdd'}">
        <TspButton type="add" name="添加" margin="true"></TspButton>
      </router-link>
      <TspButton type="delete" name="删除" :margin="false" @onClick="onDeleteBefore"></TspButton>
    </div>
    <div class="tsp-content">
      <el-table :header-row-style="{background:'#efefef'}" :header-cell-style="{background:'#efefef'}"
                :data="dataList.resultList" stripe
                @selection-change="handleSelectionChange" @filter-change="onFilterChange"
                @sort-change="onSortChange">
        <el-table-column type="selection" header-align="center" align="center" width="80">
        </el-table-column>
        <el-table-column label="序号" type="index" width="100">
        </el-table-column>
        <el-table-column label="VIN码" min-width="150" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="ICCID号" min-width="150" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createUser }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车型" min-width="150">
          <template slot-scope="scope">
            <span class="tsp-data-color">{{ scope.row.createTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车款" min-width="150" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="车辆状态" min-width="150" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="车厂名称" min-width="150" >
          <template slot-scope="scope">
            <div>
              {{ scope.row.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="导出日志" header-align="center" align='center' width="150">
          <template slot-scope="scope">
            <router-link class="tsp-router-link"
                         :to="{name:'PlatformEdit' ,params: { storeParam: {platform:activeIndex} } }">
              <div class="tsp-bt tsp-bt-look">导出日志</div>
            </router-link>
          </template>
        </el-table-column>
      </el-table>
      <div class="tsp-flex-end tsp-pagination">
        <el-pagination
          background
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          :total="dataList.results">
        </el-pagination>
      </div>
    </div>

    <DeleteDialog :isShow="deleteObj.isShow" @handleShow="(val)=> this.deleteObj.isShow=val" :param="deleteObj"
                  @handleOk='onOk'
                  @handleCancel='onCancel'></DeleteDialog>
  </div>
</template>

<script>
  import SearchBox from "@/components/custom/SearchBox"
  import TspButton from "@/components/custom/Button"
  import Breadcrumb from "@/components/custom/Breadcrumb.vue"
  import DeleteDialog from "@/components/custom/DeleteDialog.vue"
  import TspDialog from "@/components/custom/ConfirmDialog.vue"

  export default {
    name: 'PlatformManagement',
    components: {
      SearchBox,
      TspButton,
      Breadcrumb,
      DeleteDialog,
      TspDialog
    },
    data() {
      return {
        keyWord: '',
        index: 0,
        pageSize: 10,
        ascending: null,
        activeIndex: '0',
        dataList: {
          results: 0,
          pageCount: 0,
          pageSize: 10,
          currentPage: 1,
          resultList: [{}]
        },
        selects: [],
        routes: [],
        deleteObj: {
          isShow: false,
          text: '',
          type:
            'confirm',
        },
      }
    },
    methods: {
      handleSelect(key, keyPath) {
        this.activeIndex = keyPath[0];
      },
      onFilterChange(filters) {
        console.log(filters);
        if (filters['loginStatus']) {
          this.loginStatus = filters['loginStatus'][0] ? filters['loginStatus'][0] : '';
        }
        this.getDataList()
      },

      onQuery(row) {
        this.post(this.tspRlGAL, {
          id: row.roleId,
        }, (res) => {
          if (res.data.success) {
            this.sharedAuths.dialogVisible = true;
            this.sharedAuths.authList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      onSortChange(sort) {
        console.log(sort);
        this.ascending = sort.order;
        this.getDataList()
      },
      showDialog(type = 'confirm') {
        this.deleteObj.isShow = true;
        if (type == 'confirm') {
          this.deleteObj.type = type;
        } else {
          this.deleteObj.type = type;
        }
      },
      onOk() {
        this.onDelete();
      },
      onCancel() {

      },
      onDeleteBefore() {
        if (this.selects && this.selects.length > 0) {
          this.showDialog();
        } else {
          this.$message({showClose: true, message: '请选择要删除的数据!', type: 'warning'});
        }
      },
      onDelete() {
        let ids = [];
        for (let obj of this.selects) {
          ids.push(obj.id);
        }
        this.post(this.tspQtDQ, {
          ids: ids.toString(),
        }, (res) => {
          if (res.data.success) {
            this.$message({showClose: true, message: '删除成功！', type: 'success'});
            this.getDataList();
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },

      onSearch(words) {
        this.keyWord = words;
        this.index = 0;
        this.getDataList();
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getDataList();
      },
      handleCurrentChange(val) {
        this.index = val - 1;
        this.getDataList()
      },
      getDataList() {
        this.post(this.urlListAccounts, {
          index: this.index,
          num: this.pageSize,
          word: this.keyWord,
          ascending: this.ascending ? this.ascending == 'ascending' : ''
        }, (res) => {
          if (res.data.success) {
            this.dataList = res.data.data;
          } else {
            this.$message({showClose: true, message: res.data.message, type: 'error'});
          }
        });
      },
      handleSelectionChange(val) {
        this.selects = val;
      },
    },
    mounted() {
      this.routes = [
        {
          name: '平台管理',
          path: 'PlatformManagement',
        },
      ];
      this.activeIndex = this.routeParams() ? this.routeParams().platform : '0';
      this.getDataList();
    }
  }
</script>

<style scoped>
  .tsp-content {
    width: 100%;
  }

  .tsp-pagination {
    padding: 1rem;
  }
  .tsp-nu-bt-more {
    color: white;
    padding: 0.45rem 1.2rem;
    height: 2rem;
    font-size: 1rem;
    border-radius: 3rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    white-space: nowrap;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    margin-left: 1rem;
  }

</style>



