export default {
  install(Vue, options) {
    let initUrls = function (text) {
      let request = JSON.parse(text);
      let tspHost;
      Vue.prototype.dev = request.dev;
      if (request.dev) {
        tspHost = request.devOption.host + '/';
      } else {
        tspHost = request.host + ":" + request.port + '/';
      }
      Vue.prototype.tspHost = tspHost;

      Vue.prototype.urlLogin =  "//10.0.6.31:9820/user/loginservice/passlogin";  //用户登录
      Vue.prototype.urlPlatformDetectioList = tspHost + "platform/detection/accountPage";  //平台检测列表
      Vue.prototype.urlAccountList = tspHost + "dispatcherservice/account/info/accountPage";  //获取账号列表
      Vue.prototype.urlEditAccount = tspHost + "user/loginservice/passlogin";  //
    };

    let xhr;
    if (window.ActiveXObject) { //如果是IE浏览器
      xhr = new ActiveXObject("Microsoft.XMLHTTP");
    } else if (window.XMLHttpRequest) { //非IE浏览器
      xhr = new XMLHttpRequest();
    }
    xhr.open('GET', '../../static/local.json', false);
    xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
    xhr.setRequestHeader("Authorization", 'BeaereyJhbGci.eyJleH.7ASaHpA');
    xhr.setRequestHeader("If-Modified-Since", "0");
    xhr.send(null);
    if (xhr.readyState == 4 && xhr.status == 200) {
      initUrls(xhr.responseText);
    } else {
    }


    Vue.prototype.keyToken = "token";  //用户登录的token

    Vue.prototype.log = function (...args) {
      if (Vue.prototype.dev) {
        console.log(args)
      }
    };
    Vue.prototype.routeParams = function (URL, params) {
      try {
        if (!URL) {
          URL = this.$route.path;
          params || (params = this.$route.params.storeParam);
          if (params) {
            sessionStorage.setItem(URL, JSON.stringify(params));
          } else {
            params = JSON.parse(sessionStorage.getItem(URL));
          }
        } else {
          if (params) {
            sessionStorage.setItem(URL, JSON.stringify(params));
          } else {
            params = JSON.parse(sessionStorage.getItem(URL));
          }
        }
      } catch (e) {
        console.log(e);
      }
      console.log(URL, params);
      return params
    };
    Vue.prototype.download = function (URL, name) {
      try {
        let saveLink = document.createElement('a');
        saveLink.href = URL;
        if (name) {
          saveLink.download = name;
        } else {
          saveLink.download = URL;
        }
        saveLink.click();
      } catch (e) {
        console.log(e);
      }
    };
    Vue.prototype.localStore = function (key, value) {
      if (value) {
        localStorage.setItem(key, value);
      } else {
        return localStorage.getItem(key);
      }
    };
    Vue.prototype.getToken = function () {
      let str = this.localStore(this.keyToken);
      if (!str) {
        return ''
      }
      let token;
      try {
        token = JSON.parse(str);
      } catch (e) {
      }
      return token ? token['access_token'] : ""
    };
    Vue.prototype.post = function (url, params, callback, options = {
      emulateJSON: true,
      headers: {
        Authorization: 'Bearer ' + this.getToken(),
      }
    }) {
      this.$http.post(url, params, options).then((res) => {
        this.log(url, res);
        callback(res);
      }, (res) => {
        this.log('err == ' + url, res);
        if (res.status == 0 || res.status == 401) {
          // this.$router.push({name: 'Login'});
        } else {
          this.$message({showClose: true, message: '服务器异常，请检查网络！', type: 'error'});
        }
      });
    };
    Vue.prototype.isPhoneNumber = function (text) {
      return (/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(text));
    };
    Vue.prototype.codeMessage = function (data, pre = '') {
      if (data.code == 20000) {
      } else {
        pre += data.message;
      }
      this.$message({showClose: true, message: pre, type: 'error'});
      return pre;
    };
    Vue.prototype.clone = function (obj) {
      let str, newobj = obj.constructor === Array ? [] : {};
      if (typeof obj !== 'object') {
        return;
      } else if (window.JSON) {
        str = JSON.stringify(obj); //系列化对象
        newobj = JSON.parse(str); //还原
      } else {
        for (let i in obj) {
          newobj[i] = typeof obj[i] === 'object' ?
            this.clone(obj[i]) : obj[i];
        }
      }
      return newobj;
    };
  }
}



