<template>
  <TspDialog :isShow="visible">
    <div class="tsp-cd-main-confirm">
      <div class="tsp-cd-top">
        <img @click="onClose" src="../../assets/close-black.png" alt="" class="tsp-cd-close">
      </div>
      <div class="tsp-cd-content">
        <div class="tsp-cd-title"><b>{{param.title}}</b></div>
        <div class="tsp-cd-text">{{param.text}}</div>
        <div class="tsp-cd-bts">
          <div class="tsp-cd-bt tsp-cd-bt-cancel" @click="onCancel">取 消</div>
          <div class="tsp-cd-bt tsp-cd-bt-ok" @click="onOk">确 定</div>
        </div>
      </div>
    </div>
  </TspDialog>
</template>

<script>
  import TspDialog from "@/components/custom/Dialog"

  export default {
    name: 'ConfirmDialog',
    data() {
      return {
        visible: false
      }
    },
    components: {
      TspDialog
    },
    props: {
      param: {
        type: Object,
        default: function () {
          return {}
        }
      },
      defaultShow: {
        type: Boolean,
        default: function () {
          return false;
        }
      },
    },
    methods: {
      onClose() {
        this.visible = false;
      },
      onOk() {
        this.onClose();
        this.$emit('handleOk');
      },
      onCancel() {
        this.onClose();
        this.$emit('handleCancel');
      },
      show() {
        this.visible = true;
      }
    },
    computed: {},
    watch: {},
    mounted() {
      this.visible = this.defaultShow;
    }
  }
</script>
<style scoped>
  .tsp-cd-bt-ok {
    border: 1px solid #1ca9eb;
    color: white;
    background: #1ca9eb;
  }

  .tsp-cd-bt-cancel {
    border: 1px solid #959595;
    color: white;
    background: #959595;
  }

  .tsp-cd-bt {
    padding: 0.5rem 2rem;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 0.3rem;
    width: fit-content;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none;
  }

  .tsp-cd-title {
    font-size: 1.375rem;
  }

  .tsp-cd-text {
    margin-top: 1rem;
    margin-bottom: 1rem;
    color: #7189a2;
  }

  .tsp-cd-bts {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    padding: 1rem 2rem;
  }

  .tsp-cd-content {
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10.4375rem;
  }

  .tsp-cd-close {
    color: #5f5f5f;
    cursor: pointer;
  }

  .tsp-cd-main-confirm {
    width: 25rem;
    height: 23.125rem;
    padding: 1.1875rem;
    background: url("../../assets/share_bg.png") no-repeat;
    background-size: contain;
  }

  .tsp-cd-top {
    display: -webkit-flex;
    display: flex;
    justify-content: flex-end;
  }

</style>



